$breakpoint-mobile-small: 480px;
$breakpoint-mobile-large: 768px;
$breakpoint-tablet: 1024px;


body {
  margin: 0;
  padding: 50px 100px;
  color: white;
  overflow-x: hidden;
}

.home-background {
  background: url('../assets/fond_home_Plan\ de\ travail\ 1.svg');
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
}

.realisation-background {
  background: url('../assets/background\ realisations_Plan\ de\ travail\ 1.svg');
  background-size: cover;
  background-repeat: no-repeat;
}

@media (max-width: $breakpoint-tablet) {
  body {
    padding: 20px 40px;
  }

  .home-background {
    background-attachment: fixed;
  }
}

@media (max-width: $breakpoint-mobile-large) {
  body {
    padding: 10px 20px;
  }
}

@media (max-width: $breakpoint-mobile-small) {
  body {
    padding: 5px 10px;
  }
}